<template>
	<div id="uploadFile">
		<span>
			<input type="file"  @change="changeFile">
			<button>Выбрать Фаил</button>
		</span>

		<div>
			{{ resultAjax }}
		</div>
	</div>
</template>

<script>
export default {
	name: 'upload-file',
	data () {
		return {
			files:0,
			resultAjax: "",
		}
	},
	methods: {
		changeFile: function (e) {

			// ============================
			// Upload new XML 
			// ============================

			let name = e.srcElement.files[0].name.split(".");


			name[1] === "xml" ? this.files = e.srcElement.files : this.resultAjax = "Неверный тип файла";

			if (this.files) {

				var data = new FormData();

				for (var i in this.files) {
					data.append(i,this.files[i]);
				}

				let vm = this;

				axios({
				  method: 'post',
				  url: './submit.php?uploadfiles',
				  data: data,
				})
				.then(function(response) {
					if (response.status == 200) {
						vm.resultAjax = "Успешно загружен";
					}
				});
			}
		},
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

	#uploadFile {
		margin-bottom: 20px;
		>span {
			width: 150px;
			height: 50px;
			margin:0 auto;
			display: block;
			position: relative;
			cursor: pointer;
			border-radius: 4px;
			overflow: hidden;
			input {
				opacity: 0;
				width: 100%;
				height: 50px;
				z-index: 10;
				position: absolute;
				top:0;
				left:0;
				cursor: pointer;
				border-radius: 4px;
			}
			input:hover ~ button{
				background:#25475F;
			}
			button {
				width: 100%;
				height: 50px;
				text-align: center;
				color:#fff;
				position: absolute;
				top:0;
				left:0;
				z-index: 5;
				cursor: pointer;
				padding:0;
				border:none;
				border-radius: 4px;
				background:#000;
				padding: 10px;
				border: none;
				background: #1f90d8;
				color: #fff;
				font-weight: bold;
				text-decoration: none;
				cursor: pointer;
				margin-bottom: 20px;
				border-radius: 4px;
				transition: all .3s;
				border: 2px solid #25475F;
			}
		}
	}
</style>


