import Vue from 'vue'
import Router from 'vue-router'
import firstStepApp from '@/components/firstStepApp'

import searchApp from '@/components/searchApp'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'first-step',
      component: firstStepApp
    },
    {
      path: '/searchApp',
      name: 'search',
      component: searchApp
    }
  ],
  mode:'history'
})


