<template>
	<div id="workBoxesApp">

		<div class="raido-holder">
			<h4>С каким файлом взаимодествовать ?</h4>

			<input checked type="radio" name="radioTeam" id="uploadFileMet"  v-on:click="useFile1()">
			<label for="uploadFileMet">Загруженый</label>

			<input type="radio" name="radioTeam" id="convertFileMet"  v-on:click="useFile2()">
			<label for="convertFileMet">Форматированый</label>

		</div>

		<button class="icon_down_open_big createButton" v-on:click="createBoxes()"></button>

		<transition name="fade">
			<span class="dangerMessege" v-if="messege">{{messege}}</span>
		</transition>

		<div class="workbox"  v-for="(box,index) in words">

				<button v-on:click="useExceptions(box,index)" v-bind:class="{activeStrict: box.useExceptions}">Режим Исключение данных слова</button>

				<transition name="fade">
					<button v-on:click="useStrict(box,index)" v-if="box.strictShow" v-bind:class="{activeStrict: box.strictFlag}">Использовать Strict Моде</button>
				</transition>
			<br>
			<br>
			<button v-on:click="startSearch(box,index)">Поиск</button>

			<div class="allWords" v-for="(word,index) in box.data">

				<input type="text" v-bind:value="word" disabled>
				<button class="icon_cancel1" v-on:click="deleteWord(box,word,index)"></button>

			</div>

			<div class="resultTable">

				<span v-if="box.result.length" v-show="!box.searchEmpty">
					{{box.result}}
				</span>
				<p>Количество совпадений {{ box.countFind }}</p>
				<span v-if="box.searchEmpty">
					Нет совпадений
				</span>
			</div>

		</div>




	</div>
</template>

<script>

	import { bus } from '@/main.js';

export default {
	name: 'work-task',
	data () {
		return {
			countWords:0,
			city:[],
			words:[],
			resultSearch:[],
			messege:"",
			title:'',
			choseTemps:[],
			arr:[],
			downloadFile:true,
			convertFile:false,
			strictWords:[],
		}
	},
	created() {

		bus.$on('changeCity',(data)=> {
			this.city = data;
		});

		bus.$on('changeTemps',(data)=> {
			this.choseTemps = data;
		});

		bus.$on('changeStrictWords',(data)=> {
			this.strictWords = data;
		});



		var arr = [];

		var xml = this.getXMLDocument('upload/workSpace.xml');

		var allRow2 = xml.getElementsByTagName('Row');

		var xml = this.getXMLDocument('upload/workSpace.xml');

		var allRow = xml.getElementsByTagName('Data');

		var obj = [];

		for (var c = 0;c < allRow.length;c++) {
			
			var test = Number.isNaN(+$(allRow[c]).text());

			test ? obj.push($(allRow[c]).text()) : obj.push(+$(allRow[c]).text());
		}

		this.arr = obj;

		this.parseArr(this.arr,$(allRow2[0]).find( "Data" ).length);

	},
	methods: {
		getXMLDocument: function (url) {
			var xml;

			if(window.XMLHttpRequest)  {
				xml = new window.XMLHttpRequest();
				xml.open("GET", url, false);
				xml.send("");  
				return xml.responseXML;

			}  else  {

				if(window.ActiveXObject) {
					xml=new ActiveXObject("Microsoft.XMLDOM");
					xml.async=false;
					xml.load(url);
					return xml;
				}  else  {
					console.error("Загрузка XML не поддерживается браузером");
					return null;
				}
			}
		},
		parseArr: function (arrXml,count) {
			var globalArr = [];

			var row = arrXml.length/count;

			for (var i = 0;i < row;i++) {

				var str = "";

				globalArr[i] = new Array();

				for (var j = 0;j < count;j++) {

					globalArr[i][j] = arrXml[(i*count)+j];

				}

			}

			this.arr = [];

			this.arr = globalArr;

			bus.$emit('changeFile',this.arr);
		},
		useFile1: function () {

			this.downloadFile = true;
			this.convertFile = false;

			var xml = this.getXMLDocument('upload/workSpace.xml');
			this.getXml(xml);
		},
		useFile2: function () {

			this.downloadFile = false;
			this.convertFile = true;

			var xml = this.getXMLDocument('convertBox/convertVersion.xml');
			this.getXml(xml);
		},
		getXml: function (xml) {

			var allRow2 = xml.getElementsByTagName('Row');


			if (this.downloadFile) {

				var xml = this.getXMLDocument('upload/workSpace.xml');

			} else {

				var xml = this.getXMLDocument('convertBox/convertVersion.xml');

			}

			var allRow = xml.getElementsByTagName('Data');

			var obj = [];

			for (var c = 0;c < allRow.length;c++) {

				var test = Number.isNaN(+$(allRow[c]).text());

				test ? obj.push($(allRow[c]).text()) : obj.push(+$(allRow[c]).text());

			}

			this.arr = obj;

			

			this.parseArr(this.arr,$(allRow2[0]).find( "Data" ).length);
		},
		createBoxes:function () {

			this.words = [];

			// this.city = createTask.choseCitys;

			// let arr = this.choseTemps;

			var result = [];

			var lastarr = [];

			function createObj (data) {

				this.data = data;
				this.result = [];
				this.strictFlag = false;
				this.searchEmpty = false;
				this.strictShow = true;
				this.useExceptions = false;
				this.countFind = 0;

			}

			if (this.choseTemps.length && !this.city.length) {
				this.messege = "";

				for (var j =0;j < this.choseTemps.length;j++) {

					result.push(this.choseTemps[j].data );

				}

				for (var k =0;k < result.length;k++) {

					let it = result[k];

					let dataItem =  {};

					dataItem = new createObj(it); 

					this.words.push(dataItem);

				}

			} else if (this.city.length && !this.choseTemps.length) {
				this.messege = "";

				for (var i =0;i < this.city.length;i++){

					result.push( this.city[i] );

				}

				for (var k =0;k < result.length;k++) {

					let it = result[k];

					let dataItem =  {};
					dataItem.data = [];
					dataItem.data.push(it);
					dataItem.result = [];
					dataItem.strictFlag = false;
					dataItem.searchEmpty = false;
					dataItem.strictShow = true;
					dataItem.useExceptions = false;

					dataItem.countFind = 0;

					this.words.push(dataItem);

				}

			} else if (this.city.length && this.choseTemps.length) {
				this.messege = "";

				for (var i =0;i < this.choseTemps.length;i++){
					for (var j =0;j < this.city.length;j++) {

						result.push(this.choseTemps[i].data ,this.city[j] );

					}

				}

				for (var k =0;k < result.length;k=k+2) {

					let item = result[k];

					let it = result[k].concat(result[k+1]);

					let dataItem =  {};

					dataItem = new createObj(it); 

					this.words.push(dataItem);

				}

			} else {

				this.messege = "Выберите Город/Шаблон";

			}
		},
		useStrict: function (item,index) {

			item.strictFlag = !item.strictFlag;
		},
		startSearch: function (item,index) {

			item.countFind = 0;

			var holder = $('.workbox')[index];

			var globalArr = [];

			var allWord = item.data;

			var globalCount;

			if (item.strictFlag) {

				globalCount = allWord.length;

				for (var i = 0;i < this.arr.length;i++) {

					let str =  this.arr[i][0];

					if (str == 0) {

						return console.warn("конец таблицы !");

					} else if (typeof str == typeof "test") {
						str = str.toLowerCase();
					} else{
						str = str + "";
					}

					let innerCount = 0;

					for (var j = 0;j < globalCount;j++) {

						var searStr = allWord[j];
						searStr = searStr.toLowerCase();


						if ( str.indexOf(searStr,0) >= 0) {

							innerCount++;

						}

					}

					let reCreateArr = str.split(" ");

					let strictCount = 0;

					for (var c =0;c < reCreateArr.length;c++) {


						if (reCreateArr[c] == "" || reCreateArr[c] == " ") {
							reCreateArr.splice(c,1);
						}
						
					}

					for (var t = 0;t < reCreateArr.length;t++) {
						for (var k = 0;k < this.strictWords.length;k++) {
							if (reCreateArr[t] == this.strictWords[k] ) {

								strictCount++;

							}
						}
					}

					if (globalCount == innerCount && (reCreateArr.length - strictCount) == globalCount) {
					
						globalArr.push(this.arr[i]);

						item.searchEmpty = false;

						item.countFind = item.countFind + 1;

						this.mathSTR(globalArr,index,item);

					} else {

						if (!globalArr.length) {
							item.searchEmpty = true;
						}
					}

				}
			} else if (item.useExceptions) {

				globalCount = allWord.length;

				for (var i = 0;i < this.arr.length;i++) {

					let str =  this.arr[i][0];

					if (str == 0) {

						return console.warn("конец таблицы !");

					} else if (typeof str == typeof "test") {
						str = str.toLowerCase();
					} else{
						str = str + "";
					}

					let innerCount = 0;

					for (var j = 0;j < globalCount;j++) {

						let searStr = allWord[j];

						searStr = searStr.toLowerCase();

						if ( str.indexOf(searStr,0) >= 0) {

							innerCount++;

						}

					}

					if (globalCount == innerCount) {

						if (!globalArr.length) {
							item.searchEmpty = true;
						}

					} else {

						globalArr.push(this.arr[i]);

						item.searchEmpty = false;
						item.countFind = item.countFind + 1;

						this.mathSTR(globalArr,index,item);

					}

				}
			} else {

				globalCount = allWord.length;

				for (var i = 0;i < this.arr.length;i++) {

					let str =  this.arr[i][0];

					if (str == 0) {

						return console.warn("конец таблицы !");

					} else if (typeof str == typeof "test") {
						str = str.toLowerCase();
					} else{
						str = str + "";
					}

					let innerCount = 0;

					for (var j = 0;j < globalCount;j++) {

						let searStr = allWord[j];

						searStr = searStr.toLowerCase();

						if ( str.indexOf(searStr,0) >= 0) {

							innerCount++;

						}

					}

					if (globalCount == innerCount) {

						globalArr.push(this.arr[i]);

						item.countFind = item.countFind + 1;

						item.searchEmpty = false;

						this.mathSTR(globalArr,index,item);

					} else {

						if (!globalArr.length) {
							item.searchEmpty = true;
						}
					}
				}




			}
		},
		mathSTR: function (arr,index,item) {

			var endArr = [];

			var count = arr[0].length;

			for (var k = 0;k < count;k++) {
				endArr[k] = 0;
			}
			if (arr.length <= 0) {
			} else {
				endArr.unshift(arr[0][0]);
				var str = '';
				for (var i = 0;i < arr.length;i++) {
					for (var j = 1; j < arr[i].length;j++) {
						if (typeof arr[i][j] == typeof str){
							if ( !isNaN(parseInt(arr[i][j])) ) {
								str = arr[i][j];
								str = str.replace(/\s+/g,'');
								str = str.replace(/,/g,'.');
								arr[i][j] = parseFloat(str)/100;
							}
						}
						endArr[j] += arr[i][j];
					}
				}
				endArr.pop();
				this.viewInfo(endArr,index,item);
			}
		},
		viewInfo: function (arr,index,item) {
			arr[0] = "____"
			item.result = arr;
		},
		useExceptions: function (item,index) {

			item.strictFlag = false;
			item.strictShow = !item.strictShow;
			item.useExceptions = !item.useExceptions;
		},
		deleteWord: function (holder,holderInner,index) {
			holder.data.splice(index,1);
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">








#workBoxesApp {
	width: 100%;
	margin-top: 50px;
	padding-bottom: 200px;
	.raido-holder {
		margin-bottom: 50px;
	}
	.icon_down_open_big {
		&:before {
			content: "\f063";
			display: inline-block;
			font: normal normal normal 14px/1 FontAwesome;
			font-size: inherit;
			text-rendering: auto;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
		}
	}
	.createButton {
		width: 100%;
		height: 60px;
		font-size: 50px;
		color:#fff;
		border-radius: 3px;
		border-color: #1f90d8;
		background: #1f90d8;
		color: #fff;
		border: 2px solid #000;
		cursor: pointer;
	}
	.workbox {
		margin-top: 10px;
		overflow: hidden;
		padding-top: 15px;
		border-bottom: 5px solid #999;
		width: 50%;
		margin:0 auto;
		&:first-of-type {
			border-top: none;
		}

		> button {
			padding: 6px;
			border: none;
			border: 2px solid #555;
			border-radius: 3px;
			border-color: #1f90d8;
			background: #1f90d8;
			color: #fff;
			border: 2px solid #000;
			cursor: pointer;
		}
		.allWords {
			width: 100%;
			max-width: 300px;
			margin-top: 10px;
			overflow: hidden;
			input {
				float: left;

				padding: 3px;
				border: none;
				border: 1px solid #555;
				border-radius: 3px;
				margin-bottom: 15px;
				padding: 7px;
				border: none;
				border: 2px solid #555;
				border-radius: 3px;
				border-color: #1f90d8;
				width: 80%;
			}
			button {
				float: right;
				margin-top: 3px;
				padding: 4px;
				border: none;
				border: 2px solid #555;
				border-radius: 3px;
				border-color: #1f90d8;
				background: #1f90d8;
				color: #fff;
				border: 2px solid #000;
				cursor: pointer;
			}
		}
		.activeStrict {
			background:#000;	
		}

	}
	.dangerMessege {
		display: block;
		width: 100%;
		font-size: 20px;
		margin:10px 0;
		text-align:center;
	}
}











</style>


