<template>

	<div id="searchApp">
		
		<h1>Search App</h1>
		
		<create-task></create-task>

		<strict-task></strict-task>

		<work-task></work-task>
	</div>

</template>

<script>

	import createTask from '@/components/searchCom/createTask.vue'

	import strictTask from '@/components/searchCom/strictApp.vue'

	import workTask from '@/components/searchCom/workBox.vue'

	export default {
		name: 'search-app',
		data () {
			return {
				msg : "Table App"
			}
		},
		methods: {
			
		},
		components: {
			createTask,
			strictTask,
			workTask

		}
	}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">

	.raido-holder  input{
		display: none;
	}
	.raido-holder  label{
		padding: 5px;
		border: none;
		background: #1f90d8;
		color: #fff;
		font-weight: bold;
		text-decoration: none;
		cursor: pointer;
		margin-bottom: 20px;
		border-radius: 4px;
		transition: all .3s;
		border:2px solid #000;
		&:hover {
			background:#000;
		}
	}
	.raido-holder  input:checked + label{
		background: #000;
	}

</style>


