<template>
	<header>
		<span><router-link to="/">{{ msk }}</router-link></span>
		<span><router-link to="/searchApp">Поиск</router-link></span>
	</header>
</template>

<script>
export default {
	name: 'header-vue',
	data () {
		return {
			msk:"Главная"
		}
	},

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
	header {
		padding:20px 0;
		background:#25475F;
		margin-bottom: 20px;
		span {
			margin-right: 20px;
			&:last-of-type {
				margin-right: 0;
			}
			a {
				color:#fff;
				text-decoration: none;
				text-transform: uppercase;
				padding:10px;
				transition:all .4s;
				border-radius: 4px;
				&:hover {
					background:#1f90d8;
				}
			}
		}
	}

</style>


