<template>

	<div id="firstStep">
		<h1> {{ msg }}</h1>

		<upload-file></upload-file>
		<convert-table></convert-table>

	</div>

</template>

<script>

	import uploadFile from '@/components/firstStep/uploadFile.vue'
	import convertTable from '@/components/firstStep/convertTable.vue'


	export default {
		name: 'first-step',
		data () {
			return {
				msg : "Table App"
			}
		},
		methods: {
			
		},
		components: {
				uploadFile,convertTable
		}
	}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">

	h1, h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: inline-block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}



	.fade-enter-active, .fade-leave-active {
	  transition: opacity 1s
	}
	.fade-enter, .fade-leave-to {
	  opacity: 0;
	}



	#convertApp ul li span {
		padding-right: 20px;
		margin-right: 20px;
		display: inline-block;
		border-right: 2px solid #25475F;
	}
	.list-item {
	  display: inline-block;
	  margin-right: 10px;
	}
	.list-enter-active, .list-leave-active {
	  transition: all .3s;
	}
	.list-enter, .list-leave-to {
	  opacity: 0;
	  transform: translateY(30px);
	}

	.buttonDown {
		display: none;
	}

	.result li {
		border-bottom: 1px solid #25475F;
		margin-bottom: 10px;
		padding-bottom: 10px;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
	}
	.result li span{
		padding:0 5px;
		border-right: 2px solid #25475F;
	}
	.result li span:first-of-type{
		border-left: 4px solid #1f90d8;
		border-right: 2px solid #25475F;

	}
	.info span{
		display: block;
		margin-bottom: 10px;
	}

</style>


