<template>
	<div id="createTask">

		<div>
			<br>
			<br>
			<br>
		</div>
		<div class="task1">
			<h4>Города\курорты</h4>
			<div class="newCity">
				<input type="text" v-model="textCity">
				<button v-on:click="addCity()">Добавить новое слово</button>
			</div>
			<ul>
				<li v-for="(city,index) in citys">{{index + 1}}. {{ city.name }} <button v-on:click="choseCity(city,index)"  class="fa fa-plus" aria-hidden="true" v-bind:class="{icon_cancel1: city.flag}"></button></li>
			</ul>

		</div>

		<div class="task2">
			<h4>Шаблоны слов</h4>

			<div class="addTemp-holder">
				<strong>Имя шаблона</strong>
				<input type="text" v-model="nameTemp">

				<strong>Слова шаблона</strong>

				<div class="tempWords" v-for="(newWord,index) in newWords">

					<input type="text" v-model="newWord.text">
					<button class="fa fa-plus" aria-hidden="true" v-on:click="addNewWord(newWord)"></button>
					<button class="fa fa-times" aria-hidden="true" v-on:click="removeNewWord(index)" v-if="newWords.length > 1"></button>

				</div>

				<button class="addTemp" v-on:click="addTempl()">Добавить шаблон</button>

			</div>
			<div class="allTempls">

				<ul>
					<li v-for="(temp,index) in templates" >

						<span >{{ temp.name }}</span>

						<button v-on:click="choseTemp(temp,index)"  class="fa fa-plus" aria-hidden="true" v-bind:class="{icon_cancel1: temp.flag}"></button>

						<div >
							{{ temp.data }}
						</div>
					</li>
				</ul>

			</div>

		</div>
	</div>
</template>

<script>


	import { bus } from '@/main.js';


export default {
	name: 'create-task',
	data () {
		return {
			citys:[
				{
					name:"Карло",
					flag:false
				},
				{
					name:"Санат",
					flag:false
				},
				{
					name:"Лечен",
					flag:false
				}
			],
			textCity:"",
			choseCitys:[],
			templates:[
				{
					name:"Шаблон 1",
					data:[
						"лечен",
						"вар",
						"тур"
					],
					flag:false,
					visible:false
				},
			],
			newWords: [
				{
					text:"",
				},
			],
			choseTemps:[],
			wordTemp:"",
			nameTemp:"",
			countHolder:1,
			visible:false,
			showBox:false,
			arr:[],
		}
	},
	methods: {
		choseCity: function (city,index) {

			city.flag = !city.flag;

			if (city.flag) {
				this.choseCitys.push(city.name);
			} else {
				for (var i =0;i < this.choseCitys.length;i++) {

					if (this.choseCitys[i] == city.name) {
						this.choseCitys.splice(i,1);
					}
				}
			}

			bus.$emit('changeCity',this.choseCitys);
		},
		choseTemp: function (temp,index) {

			temp.flag = !temp.flag; 

			if (temp.flag) {
				this.choseTemps.push(temp)
			}else {

				for (var i =0;i < this.choseTemps.length;i++) {

					if (this.choseTemps[i] == temp) {
						this.choseTemps.splice(i,1);
					}

				}
			}


			bus.$emit('changeTemps',this.choseTemps);


		},
		addCity: function () {
			this.citys.push({
				name: this.textCity,
				flag:false
			});
			this.textCity = "";
		},
		addNewWord: function(newWord) {
			this.newWords.push({ number: newWord.number });
		},
		removeNewWord: function(index) {

			this.newWords.splice(index,1);
		},
		addTempl: function () {

			let arr = [];
			for (var i = 0;i < this.newWords.length;i++) {
				arr.push(this.newWords[i].text);
			}
			this.templates.push({
				name: this.nameTemp,
				data:arr,
				flag:false,
				visible:false
			});
		},
		hoverTampl: function (temp) {
			return temp.visible = true;
		},
		hoverTamplOut: function (temp) {
			return temp.visible = false;
		},
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

	#createTask {
		overflow: hidden;
		
		.task1,
		.task2 {
			float: left;
			padding: 20px;
			border-radius: 10px;
			border: 2px solid #000;
			background: #fff;
			margin-left: 30px;
			margin-right: 30px;
			width: 400px;

			h4 {
				text-align: center;
				font-size: 20px;
			}
			.newCity,
			.addTemp-holder {

				text-align: left;
				input {
					padding: 3px;
					border: none;
					border: 1px solid #555;
					border-radius: 3px;
					margin-bottom: 15px;
					padding: 7px;
					border: none;
					border: 2px solid #555;
					border-radius: 3px;
					border-color: #1f90d8;
					width: auto;
				}
				button {
					padding: 6px;
					border: none;
					border: 2px solid #555;
					border-radius: 3px;
					border-color: #1f90d8;
					background: #1f90d8;
					color: #fff;
					border: 2px solid #000;
					cursor: pointer;
					transition:all .4s;
					&:hover {
						background:#000;
					}
				}
				strong {
					display: block;
					text-align: left;
				}
				.addTemp {
					margin-top: 20px;
					display: block;
					float: right;
					margin-bottom: 20px;
				}

			}
			ul {
				padding: 0;
				margin: 0;
				list-style: none;
				margin-top: 20px;
				li {
					margin-bottom: 10px;
					width: 100%;
					display: block;
					text-align: left;
					overflow-y: hidden;
					background: #eee;
					padding-left: 10px;
					border-radius: 3px;
					box-sizing:border-box;
					button {
						float: right;
						padding: 4px;
						border: none;
						border: 2px solid #555;
						border-radius: 3px;
						border-color: #1f90d8;
						background: #1f90d8;
						color: #fff;
						border: 2px solid #000;
						cursor: pointer;
						transition:all .4s;
						&:hover {
							background:#000;
						}
					}
				}
			}
			.allTempls {
				li {
					span {
						float: left;
						width: 90%;
						display: block;
						height: 27px;
						cursor: crosshair;

						display: inline-flex;
						flex-direction: row;
						justify-content: left;
						align-items: center;

					}
					> div {
						margin-top: 30px;
						margin-bottom: 10px;
					}
				}
			}
		}
	}
</style>


