<template>
	<div>
		
		
		<div class="strictApp-Button" v-on:click="upStrictOption()">Strict</div>

		<div id="strictApp" v-bind:class="{ativeStrictOption : flagStrictOption}">
				
			<h4>Настройки Strict мода</h4>
			<div class="newWord">
				<input type="text" v-model="text">
				<button v-on:click="addWord">Добавить новое слово</button>
			</div>

			<ul>
				<li v-for="(word,index) in words">{{index + 1}}. {{ word }} <button v-on:click="delWord(word,index)" class="icon_cancel"></button></li>
			</ul>

		</div>
	</div>
</template>

<script>


	import { bus } from '@/main.js';


export default {
	name: 'ctrict-task',
	data () {
		return {
			flagStrictOption: false,
			words:["в","на","под"],
			text:"",
		}
	},
	created() {
		bus.$emit('changeStrictWords',this.words);
	},
	methods: {
		upStrictOption: function () {
			this.flagStrictOption = !this.flagStrictOption;
		},
		addWord: function () {
			this.words.push(this.text);
			this.text = "";

			bus.$emit('changeStrictWords',this.words);
		},
		delWord: function (word,index) {
			this.words.splice(index,1);

			bus.$emit('changeStrictWords',this.words);
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">







	.strictApp-Button {
		position: fixed;
		top:10px;
		right: 10px;
		width: 40px;
		height: 40px;
		background:#1f90d8;
		border-radius: 100%;
		color:#fff;
		box-sizing:border-box;
		border: 2px solid #000;
		font-size: 15px;
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		cursor: pointer;

	}




	#strictApp {
		position: absolute;
		width: auto;
		top:70px;
		right: 0;
		padding:20px;
		border-radius: 10px;
		border:2px solid #000;
		background: #fff;
	}

	#strictApp h4 {
		text-align: center;
		font-size: 20px;
	}
	#strictApp ul{
		padding:0;
		margin:0;
		list-style: none;
		margin-top: 20px;
	}

	#strictApp ul li{
		margin-bottom: 10px;
		width: 100%;
		display: block;
		text-align: left;
		overflow-y: hidden;
		background: #eee;
		padding-left: 10px;
		border-radius:3px;
		overflow: hidden;
		box-sizing: border-box;
	}
	#strictApp ul li button{
		float: right;
		padding:4px;
		border:none;
		border:2px solid #555;
		border-radius:3px;
		border-color:#1f90d8;
		background: #1f90d8;
		color:#fff;
		border:2px solid #000;
		cursor: pointer;
	}

	#strictApp .newWord input{
		padding: 3px;
		border: none;
		border: 1px solid #555;
		border-radius: 3px;
		margin-bottom: 15px;
		padding: 7px;
		border: none;
		border: 2px solid #555;
		border-radius: 3px;
		border-color: #1f90d8;

	}
	#strictApp .newWord button{
		padding:6px;
		border:none;
		border:2px solid #555;
		border-radius:3px;
		border-color:#1f90d8;
		background: #1f90d8;
		color:#fff;
		border:2px solid #000;
		cursor: pointer;
	}



	#strictApp {
		// position: relative;
		top:10%;
		right: -100%;
		position: fixed;
		// left:0;
		margin-bottom: 50px;
		transition:all .5s;
	}
	.ativeStrictOption {
		right: 0% !important;
	}







</style>


