<template>
	<div id="app">
		
		<header-app></header-app>

		<div class="logo">
			<svg version="1.1" id="Слой_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
	 viewBox="0 0 320 320" style="enable-background:new 0 0 320 320;" xml:space="preserve">

			<rect x="117.7" y="87.7" class="st0" width="97.3" height="219.3"/>
			<rect y="17.7" class="st0" width="320" height="70"/>
			<rect x="130.8" y="74.2" class="st1" width="71" height="220"/>
			<rect x="9.7" y="29.2" class="st1" width="300" height="45"/>
			</svg>
		</div>


		<router-view></router-view>
	</div>
</template>

<script>

	import headerApp from '@/components/header.vue'


	export default {

		name: 'app',
		components: {
			'header-app':headerApp
		}

	}

</script>

<style lang="scss">

	body {
		margin:0;
		background:#eee;
	}
	#app {
		font-family: 'Avenir', Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-align: center;
		color: #2c3e50;
		margin-top: 00px;
	}
	#app img{
		width: 250px;
	}
	.logo {
		svg {
			transition:all .4s;
		}
	}
	.st0{fill:#25475F;}
	.st1{
		fill:#1F90D8;
		transition:all .4s;
	}
	.logo {
		width: 190px;
		margin:0 auto;
		&:hover {
			svg {
				transform: scale(1.2);
			}
			.st0{
				fill:#25475F;
			}
			.st1{
				box-shadow: 0 0 0 20px #25475F;
			}
		}
	}
	.icon_cancel1,
	.icon_cancel {
			&:before {
				content: "\f00d" !important; 
				display: inline-block;
				font: normal normal normal 14px/1 FontAwesome;
				font-size: inherit;
				text-rendering: auto;
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
			}
		}
</style>


